//引入横排组件
import horizontallylist from "@/components/sortinglist/horizontallylist.vue"
export default{
	data(){
		return {
			//这是横排的数据
			horizontallylist:[],
		}
	},
	components:{
		horizontallylist
	},
	created() {
		const _this = this
		uni.request({
			url:"http://hbk.huiboke.com/api/good/getRandomRecommendGoodsList",
			data:{
				limit:20
			},
			success(res) {
				if(res.data.code==0){
					_this.horizontallylist = res.data.data
				}
			}
		})
	}
}
